import React, { useContext, useState } from 'react'
import { AppBar, Avatar, Box, Button, Container, IconButton, Menu, MenuItem, Toolbar, Tooltip, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { useLocation, useNavigate } from 'react-router-dom';
import { LoginContext } from '../../Context/LoginContext';

const pages = ['SHOP', 'SERVICES'];
// const settings = ['Profile', 'Account', 'Logout'];
const settings = [
    {
        name: 'Profile',
        link: ''
    },
    {
        name: 'Account',
        link: ''
    },
    {
        name: 'Logout',
        link: ''
    }
]


const Navbar = () => {
    const location = useLocation();
    const { login, setLogin } = useContext(LoginContext)
    const navigate = useNavigate();
    const [anchorElNav, setAnchorElNav] = useState(null);
    const [anchorElUser, setAnchorElUser] = useState(null);

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };
    const handleClick = (value) => {
        if (value === 'SHOP') {
            navigate("/shop");
            console.log(value);
        }
        else if (value === 'SERVICES') {
            navigate("/services")
            console.log(value);
        }
        else {
            console.log(value);
        }
        setAnchorElNav(null);

    }
    const handleAccountClick = (name) => {
        if (name === 'Profile') {
            navigate("/profile");
            console.log(name);
        }
        else if (name === 'Account') {
            navigate("/account");
            console.log(name);
        }
        else {
            localStorage.removeItem("username");
            setLogin("");
            console.log(name);
        }
        setAnchorElUser(null);
    }
    const handleCloseUserMenu = () => {
        setAnchorElUser(null);
    }
    const handleLogin = () => {
        navigate("/login");
    }
    return (
        <AppBar sx={{ background: 'white', color: 'black' }} position='static'>
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    <Typography variant="h6" noWrap component="a" href="/" sx={{
                        mr: 2, display: { xs: 'none', md: 'flex' }, fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'inherit', textDecoration: 'none',
                    }}
                    >
                        LOGO
                    </Typography>

                    <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                        <IconButton size="large" aria-label="account of current user" aria-controls="menu-appbar" aria-haspopup="true" onClick={handleOpenNavMenu} color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                                color: 'black'
                            }}
                        >
                            {pages.map((page) => (
                                <MenuItem key={page} onClick={() => handleClick(page)}>
                                    <Typography textAlign="center">{page}</Typography>
                                </MenuItem>
                            ))}
                        </Menu>
                    </Box>
                    {/* <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} /> */}
                    {/* <Link to="/"> */}
                        <Typography variant="h5" noWrap component="a" href="/" sx={{ mr: 2, display: { xs: 'flex', md: 'none' }, flexGrow: 1, fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'inherit', textDecoration: 'none', }}
                        >
                            LOGO
                        </Typography>
                    {/* </Link> */}
                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                        {pages.map((page) => (
                            <Button
                                key={page}
                                onClick={() => handleClick(page)}
                                sx={{ my: 2, color: 'black', display: 'block' }}
                            >
                                {page}
                            </Button>
                        ))}
                    </Box>
                    {
                        login
                            ?
                            <Box sx={{ flexGrow: 0 }}>
                                <Tooltip title="Open settings">
                                    <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                                        {/* <Avatar alt="Hemy Sharp" src="https://media.istockphoto.com/photos/generic-human-man-face-front-view-futuristic-picture-id907671090?k=6&m=907671090&s=170667a&w=0&h=S8gQNgTy6X1dEkWah9sC3Ru-3AbZXG_5wa6ibzrQzR8=" /> */}
                                        <Avatar alt={login} src="/fdsf" />
                                    </IconButton>
                                </Tooltip>
                                <Menu
                                    sx={{ mt: '45px' }}
                                    id="menu-appbar"
                                    anchorEl={anchorElUser}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    open={Boolean(anchorElUser)}
                                    onClose={handleCloseUserMenu}
                                >
                                    {settings.map((setting, index) => (
                                        <MenuItem key={index} value={setting.name} onClick={(e) => handleAccountClick(setting.name)}>
                                            <Typography textAlign="center">{setting.name}</Typography>
                                        </MenuItem>
                                    ))}
                                </Menu>
                            </Box>
                            :
                            location.pathname !== '/login' &&
                            <Button onClick={handleLogin} variant="contained">Log In</Button>
                    }

                </Toolbar>
            </Container>
        </AppBar>
    )
}

export default Navbar
