import { Box, Button, Stack, TextField, Typography } from '@mui/material'
import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { LoginContext } from '../../Context/LoginContext'



const Login = () => {
    const navigate = useNavigate();
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const { setLogin } = useContext(LoginContext);
    const handleLogin = (e) => {
        e.preventDefault();
        navigate("/");
        localStorage.setItem("username",name)
        setLogin(localStorage.getItem("username"));
    }
    
    return (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '80vh' }}>
            <Stack sx={{ padding: 10, display: 'flex', gap: 2 }}>
                {/* <form> */}
                    <Typography sx={{ fontSize: 30, textAlign: 'center', margin: 2 }}>Log In</Typography>
                    <TextField value={name} onChange={(e) => setName(e.target.value)} id="name" label="Name" variant="outlined" />
                    <TextField type={"email"} value={email} onChange={(e) => setEmail(e.target.value)} id="email" label="Email" variant="outlined" />
                    <TextField autoComplete="on" value={password} onChange={(e) => setPassword(e.target.value)} type={"password"} id="password" label="Password" variant="outlined" />
                    {/* <TextField id="outlined-basic" label="Outlined" variant="outlined" /> */}
                    <Button onClick={handleLogin} sx={{ marginTop: 2 }} variant='contained'>Log In</Button>
                {/* </form> */}
                <Typography sx={{cursor:'pointer',fontSize:{xs:13,md:16}}} onClick={()=>{navigate("/register")}}>Don't have an account? Click here</Typography>
            </Stack>
        </Box>
    )
}

export default Login
