import { Grid, Stack, styled, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'


const LinkTo = styled(Link)(({theme})=>({
    color:'white',
    textDecoration:'none',
    lineHeight:'2'
}))



const Footer = () => {
    return (
        <Grid container sx={{ background: 'black', height: 'auto', padding: 7,display:'flex',justifyContent:{md:"space-between"},alignItems:'center',gap:{xs:5,sm:0}}} >
            <Grid xs={12} sm={6} md={3} sx={{marginBottom:{sm:'50px',md:0}}} item>
                <Stack direction={'column'}>
                    <Typography variant="h6" noWrap component="a" href="/" sx={{
                        fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'white', textDecoration: 'none',
                    }}
                    >
                        LOGO
                    </Typography>
                    <LinkTo to="#!">Home</LinkTo>
                    <LinkTo to="#!">Shop</LinkTo>
                    <LinkTo to="#!">Services</LinkTo>
                    <LinkTo to="#!">Blog</LinkTo>
                    <LinkTo to="#!">Contact us</LinkTo>
                </Stack>
            </Grid>
            <Grid xs={12} sm={6} md={3} sx={{marginBottom:{sm:'50px',md:0}}} item>
            <Stack direction={'column'}>
                    <Typography variant="h6" noWrap component="a" href="/" sx={{
                        fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'white', textDecoration: 'none',
                    }}
                    >
                        LOGO
                    </Typography>
                    <LinkTo to="#!">Home</LinkTo>
                    <LinkTo to="#!">Shop</LinkTo>
                    <LinkTo to="#!">Services</LinkTo>
                    <LinkTo to="#!">Blog</LinkTo>
                    <LinkTo to="#!">Contact us</LinkTo>
                </Stack>
            </Grid>
            <Grid xs={12} sm={6} md={3} item>
            <Stack direction={'column'}>
                    <Typography variant="h6" noWrap component="a" href="/" sx={{
                        fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'white', textDecoration: 'none',
                    }}
                    >
                        LOGO
                    </Typography>
                    <LinkTo to="#!">Home</LinkTo>
                    <LinkTo to="#!">Shop</LinkTo>
                    <LinkTo to="#!">Services</LinkTo>
                    <LinkTo to="#!">Blog</LinkTo>
                    <LinkTo to="#!">Contact us</LinkTo>
                </Stack>
            </Grid>
            <Grid xs={12} sm={6} md={3} item>
            <Stack direction={'column'}>
                    <Typography variant="h6" noWrap component="a" href="/" sx={{
                        fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'white', textDecoration: 'none',
                    }}
                    >
                        LOGO
                    </Typography>
                    <LinkTo to="#!">Home</LinkTo>
                    <LinkTo to="#!">Shop</LinkTo>
                    <LinkTo to="#!">Services</LinkTo>
                    <LinkTo to="#!">Blog</LinkTo>
                    <LinkTo to="#!">Contact us</LinkTo>
                </Stack>
            </Grid>
        </Grid>
    )
}

export default Footer
