import { Button, Grid, styled, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';

const Items = [
    {
        text: "Become a Pet Owner",
    },
    {
        text: "Get Your vaccination done",
    },
    {
        text: "Consult a Vet",
    }
]

const GridContain = styled(Grid)(({ theme }) => ({
    height: 'auto',
    padding: '50px 0',
    margin: '10px 0',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    width: '100%',
    // background: "url('https://www.pixelstalk.net/wp-content/uploads/2016/07/Landscape-mountains-nature-fog-pictures.jpg')",
    // backgroundSize: 'cover',
    // backgroundRepeat: 'no-repeat',
    backgroundSize: "100% 100%",
    backgroundPosition: "0px 0px,0px 0px,0px 0px,0px 0px,0px 0px",
    backgroundImage:
        "repeating-linear-gradient(315deg, #00FFFF2E 92%, #073AFF00 100%),repeating-radial-gradient(75% 75% at 238% 218%, #00FFFF12 30%, #073AFF14 39%),radial-gradient(99% 99% at 109% 2%, #00C9FFFF 0%, #073AFF00 100%),radial-gradient(99% 99% at 21% 78%, #7B00FFFF 0%, #073AFF00 100%),radial-gradient(160% 154% at 711px -303px, #2000FFFF 0%, #073AFFFF 100%)"

}))

const GridItem = styled(Grid)(({ theme }) => ({
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center'
}))

const Sub = styled(Typography)(({theme})=>({
    // fontSize:{xs:12,sm:16}
    [theme.breakpoints.down("300px")]:{
        fontSize:10
    }
}))

const Info = () => {
    return (
        <Grid sx={{ height: 'auto' }} container>
            {
                Items.map((item, key) => (
                    <GridContain key={key} item>

                        <GridItem item>
                            <Link style={{ textDecoration: 'none' }} to="#!">
                                <Typography sx={{ color: 'white', fontSize: { xs: 25, sm: 30, md: 40 } }}>{item.text}</Typography>
                            </Link>
                            <Sub sx={{fontSize:{xs:12,sm:16}}} color="white">
                                This is the place where we will do something creative
                            </Sub>
                            <Button style={{ margin: '10px 0',background:'orange' }} variant="contained">Click Here <ArrowRightAltIcon /> </Button>
                        </GridItem>
                    </GridContain>
                ))
            }
        </Grid>
    )
}

export default Info
