import { Box, Grid, styled, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'

const ImgSrc1 = [
    {
        img: "https://www.pixelstalk.net/wp-content/uploads/2016/07/Landscape-mountains-nature-fog-pictures.jpg",
        sale: "Up to 25%Off"
    },
    {
        img: "https://www.pixelstalk.net/wp-content/uploads/2016/07/Landscape-mountains-nature-fog-pictures.jpg",
        sale: "Up to 25%Off"
    },
]

const Container = styled(Box)(({ theme }) => ({
    // display:'flex',
    textAlign: 'center'
}))


const LinkTo = styled(Link)(({ theme }) => ({
    textDecoration: 'none'
}))







const Poster = () => {
    return (
        <Container>
            <Typography
                sx={{ color: 'gold', fontSize: { xs: 30, sm: 30, md: 40 } }}
            >Brand In Trend</Typography>
            <Typography sx={{ fontSize: 20, marginBottom: 4 }}>You Love this</Typography>
            {/*------------------------- 1st Container  -----------------------*/}

            <Grid sx={{ margin: 'auto', width: '90%', display: 'flex', justifyContent: 'space-around' }} container>
                {
                    ImgSrc1.map((item, key) => (
                        <Grid key={key} xs={12} sm={10} md={5} sx={{ margin: { xs: "20px 0", lg: '0px' }, height: 300, width: 500, background: 'rgba(204, 200, 202, 0.5)', }} item>
                            <LinkTo to="#!">
                                <img alt="poster" width={'100%'} height={250} src={item.img} />

                                <Typography sx={{ color: 'black', fontSize: 20, margin: '10px 0 0 0' }}>{item.sale}</Typography>
                            </LinkTo>
                        </Grid>
                    ))
                }

            </Grid>
            {/*-------------------------------- 2nd Container ---------------------*/}
            <Grid sx={{ margin:{xs:'0 auto',md:'30px auto'}, width: '90%', display: 'flex', justifyContent: 'space-around' }} container>
            {
                    ImgSrc1.map((item, key) => (
                        <Grid key={key} xs={12} sm={10} md={5} sx={{ margin: { xs: "20px 0", lg: '0px' }, height: 300, width: 500, background: 'rgba(204, 200, 202, 0.5)', }} item>
                            <LinkTo to="#!">
                                <img alt="poster" width={'100%'} height={250} src={item.img} />

                                <Typography sx={{ color: 'black', fontSize: 20, margin: '10px 0 0 0' }}>{item.sale}</Typography>
                            </LinkTo>
                        </Grid>
                    ))
                }

            </Grid>
        </Container>
    )
}

export default Poster
